<template>
  <body :class="{ debug: isDev }">
    <header>
      <b-navbar toggleable="md" type="light" variant="light" id="top-bar">
        <b-navbar-brand to="/">Balance Share</b-navbar-brand>

        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

        <b-collapse is-nav id="nav_collapse">

          <!-- LOGGED USER -->
          <template v-if="user">
            <b-navbar-nav class="ml-auto">
              <b-nav-item-dropdown :text="user.name" right>
                <b-dropdown-item to="/change">Change Password</b-dropdown-item>
              </b-nav-item-dropdown>
              <b-nav-item @click="logout">Logout</b-nav-item>
            </b-navbar-nav>
          </template>

          <!-- GUEST -->
          <template v-else>
            <b-navbar-nav v-if="isDev">
              <b-nav-item to="/prototype/companies" exact>Companies</b-nav-item>
              <b-nav-item to="/prototype/company" exact>Company Profile</b-nav-item>
              <b-nav-item to="/prototype/nomenclatures" exact>Nomenclatures</b-nav-item>
              <b-nav-item to="/matching-tool" exact>Matching Tool</b-nav-item>
              <b-nav-item-dropdown text="auth pages" right>
                <b-dropdown-item to="/login">Login</b-dropdown-item>
                <b-dropdown-item to="/register">Register</b-dropdown-item>
                <b-dropdown-item to="/forgot">Forgot pass</b-dropdown-item>
                <b-dropdown-item to="/reset">Reset pass</b-dropdown-item>
              </b-nav-item-dropdown>
            </b-navbar-nav>
          </template>

        </b-collapse>

      </b-navbar>
    </header>

    <router-view></router-view>

    <footer>
    </footer>

    <div v-if="isDev" id="dev-toolbox">
      <b-btn v-b-toggle.collapseDevToolbox variant="outline-danger">* Dev Toolbox</b-btn>
      <b-collapse id="collapseDevToolbox" class="mt-2">
        <b-card>
          <button type="button" class="btn btn-danger btn-sm" @click="test">Test Action</button>
          <div slot="footer">
            {{ buildTimestamp }}
          </div>
        </b-card>
      </b-collapse>
    </div>

  </body>
</template>


<script>
export default {
  data() {
    return {
      isDev: process.env.APP_DEBUG,
      buildTimestamp: process.env.BUILD_TIMESTAMP,
    }
  },
  methods: {
    test() {
      console.log('Test')
    },
  },
  computed: {
    user() {
      return null
    },
  },
}
</script>



