import axios from 'axios'
import {apiToCompany} from './converters'

const API_V1_URL = process.env.API_V1_URL

export default {
  // getCompanies() {}, ?? detailed objects
  getList() { // ?? simple list
    return axios.get(API_V1_URL + '/companies/')
      .then(data => data.map(apiToCompany))
  },
  getCompany(id) {
    return axios.get(API_V1_URL + '/company/' + id)
      .then(data => apiToCompany(data))
  },
  create(data) {},
  update(data) {},
  delete(id) {},
}



