<template>
  <form @submit.prevent="submit">

    <div>
      <button type="submit" class="btn btn-primary">Save</button>
      <button type="button" class="btn btn-secondary">Cancel</button>
    </div>

    <div class="form-group">
      <label for="name">Company Name:</label>
      <input type="text" class="form-control" id="name" placeholder="Company Name" required v-model="companyForm.name">
      <small class="form-text text-muted">The complete registered name.</small>
    </div>

    <div class="form-row">
      <div class="form-group col">
        <label for="country">Country:</label>
        <select class="form-control" id="country" required v-model="companyForm.country">
          <option selected>Choose...</option>
          <option value="1">USA</option>
          <option value="2">UK</option>
          <option value="3">Hong Kong</option>
        </select>
      </div>

      <div class="form-group col">
        <label for="currency">Reporting Currency:</label>
        <select class="form-control" id="currency" required v-model="companyForm.currency">
          <option selected>Choose...</option>
          <option value="1">USD</option>
          <option value="2">EUR</option>
          <option value="3">BCH</option>
        </select>
      </div>
    </div>


    <!-- REGISTRATION ID -->
    <div class="form-row" v-if="companyForm.regId || companyForm.regId === ''">

      <button type="button" class="btn btn-danger btn-sm" @click="companyForm.regId = null">-</button>

      <div class="form-group col">
        <label for="regId">Registration ID:</label>
        <input type="text" class="form-control" id="regId" placeholder="Registration ID" v-model="companyForm.regId">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="companyForm.regId = ''" v-if="companyForm.regId === null">+ Add Registration ID</button>
    </div>


    <!-- ALIAS -->
    <div class="form-row" v-for="(alias, index) in companyForm.aliases">

      <button type="button" class="btn btn-danger btn-sm" @click="removeAlias(index)">-</button>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Alias" v-model="alias.value">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addAlias">+ Add Alias</button>
    </div>


    <!-- CORPORATE TAX -->
    <div class="form-row" v-if="showCorpTax">

      <button type="button" class="btn btn-danger btn-sm" @click="removeCorpTax">-</button>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Corporate Tax ID" v-model="companyForm.corpTax.id">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Filing Deadline" v-model="companyForm.corpTax.filingDate">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Due Date" v-model="companyForm.corpTax.dueDate">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Advanced Payments: to do ..." v-model="companyForm.corpTax.advancedPayments">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addCorpTax" v-if="!showCorpTax">+ Add Corporate Tax</button>
    </div>


    <!-- VAT -->
    <div class="form-row" v-for="(vat, index) in companyForm.vats">

      <div class="form-group-col">
        <button type="button" class="btn btn-danger btn-sm" @click="removeVAT(index)">-</button>
      </div>

      <div class="form-group col">
        <select class="form-control" v-model="vat.country">
          <option value="1">European Empire</option>
          <option value="2">United States</option>
          <option value="3">United Kingdom</option>
          <option value="4">Hong Kong</option>
        </select>
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" :placeholder="(vat.country === 1) ? 'VAT ID' : 'Sales Tax ID'" v-model="vat.id">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Reporting Regularity: todo ..." v-model="vat.reportingRegularity">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addVAT">+ Add VAT / Sales Tax</button>
    </div>


    <!-- AUDIT -->
    <div class="form-row" v-if="showAudit">

      <button type="button" class="btn btn-danger btn-sm" @click="removeAudit">-</button>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Auditor's Name" v-model="companyForm.audit.name">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Auditor's Email" v-model="companyForm.audit.email">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Auditor's Phone" v-model="companyForm.audit.phone">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Audit Deadline: to do ..." v-model="companyForm.audit.ddl">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addAudit" v-if="!showAudit">+ Add Audit Info</button>
    </div>


    <!-- PERSONNEL -->
    <div class="form-row" v-for="(responsible, index) in companyForm.personnel">

      <div class="form-group-col">
        <button type="button" class="btn btn-danger btn-sm" @click="removeResponsible(index)">-</button>
      </div>

      <div class="form-group col">
        <select class="form-control" v-model="responsible.label">
          <option value="1">Executive Director</option>
          <option value="2">Non-Executive Director</option>
          <option value="3">Accountant</option>
          <option value="4">Payroll Manager</option>
          <option value="5">Legal Advisor</option>
          <option value="6">Tax Advisor</option>
        </select>
        <small class="form-text text-muted">TODO: add custom option</small>
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Name" v-model="responsible.name">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Email" v-model="responsible.email">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Phone" v-model="responsible.phone">
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addResponsible">+ Add Personnel</button>
    </div>


    <!-- TAXES -->
    <div class="form-row" v-for="(tax, index) in companyForm.taxes">

      <button type="button" class="btn btn-danger btn-sm" @click="removeTax(index)">-</button>

      <div class="form-group col">
        <select class="form-control" v-model="tax.label">
          <option value="1">Withholding tax</option>
          <option value="2">Municipality tax</option>
          <option value="3">Bai Hui Tax</option>
        </select>
        <small class="form-text text-muted">TODO: add custom option</small>
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Filing Deadline" v-model="tax.filingDate">
      </div>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Due Date" v-model="tax.dueDate">
      </div>

      <div class="form-group col">
        <textarea class="form-control" placeholder="Info" v-model="tax.value" rows="3"></textarea>
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addTax">+ Add Tax</button>
    </div>


    <!-- SOFTWARE -->
    <div class="form-row" v-for="(software, index) in companyForm.software">

      <button type="button" class="btn btn-danger btn-sm" @click="removeSoftware(index)">-</button>

      <div class="form-group col">
        <select class="form-control" v-model="software.label">
          <option value="1">ERP</option>
          <option value="2">CRM</option>
          <option value="3">Invoicing</option>
        </select>
        <small class="form-text text-muted">TODO: add custom option</small>
      </div>

      <div class="form-group col">
        <textarea class="form-control" placeholder="Info" v-model="software.value" rows="3"></textarea>
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addSoftware">+ Add Software</button>
    </div>


    <!-- MISC -->
    <div class="form-row" v-for="(field, index) in companyForm.misc">

      <button type="button" class="btn btn-danger btn-sm" @click="removeField(index)">-</button>

      <div class="form-group col">
        <input type="text" class="form-control" placeholder="Label" v-model="field.label">
      </div>

      <div class="form-group col">
        <textarea class="form-control" placeholder="Info" v-model="field.value" rows="3"></textarea>
      </div>

    </div>

    <div class="mb-4">
      <button type="button" class="btn btn-success btn-sm" @click="addField">+ Add Field</button>
    </div>

  </form>
</template>


<script>

const INITIAL_COMPANY_DATA = {
  name: null,
  country: null,
  currency: null,
  regId: null,
  aliases: [],
  vats: [],
  corpTax: null,
  audit: null,
  personnel: [],
  taxes: [],
  software: [],
  misc: [],
}

export default {
  props: {
    'company': {
      default() {
        return {
          ...INITIAL_COMPANY_DATA,
        }
      },
    },
  },
  data() {
    return {
      companyForm: this.company,
      showCorpTax: false,
      showAudit: false,
    }
  },
  methods: {
    // Alias
    addAlias() {
      this.companyForm.aliases.push({
        value: null,
      })
    },
    removeAlias(index) {
      this.companyForm.aliases.splice(index, 1)
    },
    // Corporate Tax
    addCorpTax() {
      this.companyForm.corpTax = {}
      this.showCorpTax = true
    },
    removeCorpTax() {
      this.showCorpTax = false
      this.companyForm.corpTax = null
    },
    // VAT
    addVAT() {
      this.companyForm.vats.push({
        country: 1,
      })
    },
    removeVAT(index) {
      this.companyForm.vats.splice(index, 1)
    },
    // Audit
    addAudit() {
      this.companyForm.audit = {}
      this.showAudit = true
    },
    removeAudit() {
      this.showAudit = false
      this.companyForm.audit = null
    },
    // Responsible
    addResponsible() {
      this.companyForm.personnel.push({
        label: 1,
      })
    },
    removeResponsible(index) {
      this.companyForm.personnel.splice(index, 1)
    },
    // Tax
    addTax() {
      this.companyForm.taxes.push({
        label: 1,
      })
    },
    removeTax(index) {
      this.companyForm.taxes.splice(index, 1)
    },
    // Software
    addSoftware() {
      this.companyForm.software.push({
        label: 1,
      })
    },
    removeSoftware(index) {
      this.companyForm.software.splice(index, 1)
    },
    // Field
    addField() {
      this.companyForm.misc.push({
        label: null,
      })
    },
    removeField(index) {
      this.companyForm.misc.splice(index, 1)
    },
    submit() {
      let formData = this.companyForm
      let companyData = {}
      let arrsObj = {}

      arrsObj.aliases = formData.aliases.filter(alias => !!alias.value).map(alias => alias.value)
      arrsObj.vats = formData.vats.filter(vat => !!vat.id)
      arrsObj.personnel = formData.personnel.filter(responsible => !!responsible.name)
      arrsObj.taxes = formData.taxes.filter(tax => !!tax.value)
      arrsObj.softwareArr = formData.software.filter(software => !!software.value)
      arrsObj.misc = formData.misc.filter(item => !!item.value)

      Object.keys(arrsObj).forEach(key => {
        if (arrsObj[key].length) {
          companyData[key] = arrsObj[key]
        }
      })

      const keys = ['regId', 'corpTax']
      keys.forEach(key => {
        if (formData[key]) {
          companyData[key] = formData[key]
        }
      })

      // required fields
      companyData.name = formData.name
      companyData.country = formData.country
      companyData.currency = formData.currency

      console.log(companyData)
    },
  },
}
</script>



