import Vue from 'vue'
import Vuex from 'vuex'

// Modules
import company from './modules/companyStore'
import nomenclature from './modules/nomenclatureStore'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    company,
    nomenclature,
  },
})



