import Vue from 'vue'
import Router from 'vue-router'

// Pages
import Home from '@/pages/HomePage'
import MatchingTool from '@/pages/MatchingToolPage'

// Prototype Pages
import Prototype from '@/pages/prototypes/PrototypePage'
import Company from '@/pages/prototypes/CompanyPage'
import Companies from '@/pages/prototypes/CompaniesPage'
import Nomenclatures from '@/pages/prototypes/NomenclaturesPage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        pageId: 'home-page',
        pageClass: '',
      },
    },
    {
      path: '/matching-tool',
      name: 'matchingTool',
      component: MatchingTool,
      meta: {
        pageId: 'matching-tool-page',
        pageClass: '',
      },
    },
    {
      path: '/prototype',
      name: 'prototype',
      component: Prototype,
      redirect: '/prototype/company',
      meta: {
        pageId: '',
        pageClass: 'prototype-page',
      },
      children: [
        {
          path: 'companies',
          name: 'companies',
          component: Companies,
          meta: {
            pageId: 'companies-page',
            pageClass: '',
          },
        },
        {
          path: 'company',
          name: 'company',
          component: Company,
          meta: {
            pageId: 'company-page',
            pageClass: '',
          },
        },
        {
          path: 'nomenclatures',
          name: 'nomenclatures',
          component: Nomenclatures,
          meta: {
            pageId: 'nomenclatures-page',
            pageClass: '',
          },
        },
      ],
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
  mode: 'history',
})



