<template>
  <div class="container-fluid">
      <h1>Matching Tool</h1>

      <form @submit.prevent="submit">

        <div class="form-group">
          <label for="country">Statement type:</label>
          <select class="form-control" id="country" required v-model="formData.statement">
            <option value="1" selected>Balance Sheet</option>
            <option value="2">Income Statement</option>
            <option value="3">Cash Flow Statement</option>
          </select>
        </div>

        <div class="form-group">
          <label for="from">From:</label>
          <input type="text" class="form-control" id="from" required v-model="formData.from">
        </div>

        <div class="form-group">
          <label for="to">To:</label>
          <input type="text" class="form-control" id="to" required v-model="formData.to">
        </div>

        <div class="form-group">
          <label for="file1">Own Company File:</label>
          <div class="custom-file">
            <input type="file" class="custom-file-input" id="file1">
            <label class="custom-file-label" for="file1">Choose file</label>
          </div>
        </div>

        <div class="form-group">
          <label for="file2">Opposite Company File:</label>
          <div class="custom-file">
            <input type="file" class="custom-file-input" id="file2">
            <label class="custom-file-label" for="file2">Choose file</label>
          </div>
        </div>

        <div class="mt-4">
          <button type="submit" class="btn btn-primary">Submit</button>
        </div>

      </form>

  </div>
</template>


<script>
export default {
  data() {
    return {
      formData: {
        statement: 1,
      },
    }
  },
  methods: {
    submit() {
      alert('Bai Hui')
    },
  },
}
</script>



