// * * * NOMENCLATURES * * *

function apiToCountry(data) {
  return {
    id: data.id,
    name: data.name,
    code: data.code,
    order: data.order,
  }
}

function apiToCurrency(data) {
  return {
    id: data.id,
    name: data.name,
    code: data.code,
    order: data.order,
  }
}

