import nomenclatureService from '@/services/nomenclatureService'

const nomenclatures = [
  'Countries',
  'Currencies',
]

var state = {}
var getters = {}
var mutations = {}
var actions = {}

nomenclatures.forEach(nomenclature => {
  state[nomenclature] = null

  getters['get' + nomenclature] = function(state) {
    return state[nomenclature]
  }

  mutations['set' + nomenclature] = function(state, payload) {
    state[nomenclature] = payload
  }

  actions['load' + nomenclature] = function({ commit }) {
    nomenclatureService[nomenclature.toLowerCase()]()
      .then(arr => {
        commit('set' + nomenclature, arr)
        return arr
      })
  }
})

export default {
  state,
  getters,
  mutations,
  actions,
}



