import axios from 'axios'
import * as convert from './converters'

const API_V1_URL = process.env.API_V1_URL

export default {
  countries() { return axios.get(API_V1_URL + '/Countries').then(data => data.map(convert.apiToCountry)) },
  currencies() { return axios.get(API_V1_URL + '/Currencies').then(data => data.map(convert.apiToCurrency)) },
  statements() { return axios.get(API_V1_URL + '/StatementTypes/List').then(data => data.map(convert.apiToCountry)) },
  taxes() { return axios.get(API_V1_URL + '/Taxes').then(data => data.map(convert.apiToCountry)) },
  softwareTypes() { return axios.get(API_V1_URL + '/SoftwareTypes').then(data => data.map(convert.apiToCountry)) },
  responsibilities() { return axios.get(API_V1_URL + '/Responsibilities').then(data => data.map(convert.apiToCountry)) },
}



