<template>
  <table>
    <thead>
      <tr>
        <th>Name</th>
        <th>Aliases</th>
        <th>Reporting Currency</th>
        <th>Direct Parent</th>
        <th>Top Parent</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="company in companies">
        <td>{{ company.name }}</td>
        <td>{{ company.aliases.join(', ') }}</td>
        <td>{{ company.currency.code }}</td>
        <td>{{ company.parent }}</td>
        <td>{{ company.topParent }}</td>
      </tr>
    </tbody>
  </table>
</template>


<script>
export default {
  props: ['companies'],
}
</script>



