import companyService from '@/services/companyService'

const state = {
  selectedCompany: null,
  list: null,
  tree: null,
}

const getters = {
  getSelectedCompany(state) {
    return state.selectedCompany
  },
  getCompaniesList(state) {
    return state.list
  },
  getCompaniesTree(state) {
    return state.tree
  },
}

const mutations = {
  setSelectedCompany(state, payload) {
    state.selectedCompany = state.list[payload]
  },
  setList(state, payload) {
    state.list = payload
  },
  setTree(state, payload) {
    state.tree = payload
  },
}

const actions = {
  loadSelectedCompany({ commit }, {id, index}) {
    companyService.getCompany(id)
      .then(company => {
        // TODO: add the company to the list
        // and get the index
        commit('setSelectedCompany', index)
      })
  },
  loadCompanies({ commit }) {
    companyService.getList()
      .then(companies => {
        commit('setList', companies)
        commit('setTree', companies)
      })
  },
  createCompany({ commit }, data) {},
  updateCompany({ commit }, data) {},
  deleteCompany({ commit }, id) {},
}

export default {
  state,
  getters,
  mutations,
  actions,
}



